<template>
	<div>
		<h3>Todo list</h3>
		<div class="legend">
			<span>Double click to mark as complete *</span>
			<span> <span class="incomplete-box"></span> = Incomplete </span>
			<span> <span class="complete-box"></span> = Complete </span>
		</div>
		<div class="todos">
			<div
				@dblclick="onDblClick(todo)"
				v-for="todo in allTodos"
				v-bind:key="todo.id"
				class="todo"
				v-bind:class="{ isComplete: todo.completed }"
			>
				{{ todo.title }}
				<i @click="deleteTodo(todo.id)" class="fas fa-trash-alt"></i>
			</div>
		</div>
	</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
export default {
	name: 'TodoList',
	datas() {},
	methods: {
		...mapActions(['fetchTodos', 'deleteTodo', 'updateTodo']),
		onDblClick(todo) {
			const updTodo = {
				...todo,
				completed: !todo.completed
			};
			this.updateTodo(updTodo);
		}
	},
	computed: mapGetters(['allTodos']),
	created() {
		this.fetchTodos();
	}
};
</script>

<style scoped>
.todos {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 1rem;
}
.todo {
	border: 1px solid #ccc;
	background-color: #41b883;
	padding: 1rem;
	border-radius: 5px;
	text-align: center;
	position: relative;
	cursor: pointer;
}
i {
	position: absolute;
	bottom: 10px;
	right: 10px;
	color: #fff;
	cursor: pointer;
}
.legend {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 1rem;
	margin-bottom: 1rem;
}
.incomplete-box {
	display: inline-block;
	width: 10px;
	height: 10px;
	background-color: #41b883;
}
.complete-box {
	display: inline-block;
	width: 10px;
	height: 10px;
	background-color: #34495d;
}
@media (max-width: 500px) {
	.todos {
		grid-template-columns: 1fr;
	}
}
.isComplete {
	color: #fff;
	background-color: #34495d;
}
</style>



